@import "../../index.scss";

.navbar {
background: $mainBlack;
padding: 1rem   0.5rem;
margin: 1,0,1,0 rem;
display: grid;
grid-template-columns: auto  1fr;
}

.nav-links{
    align-self:center;
    list-style-type:none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
}
.nav-link{
    color: $mainGrey;
    text-transform: capitalize;
    text-decoration: none;
    padding: 1rem;

    font-size: 1.2rem;
    transition: all  0.3s  linear;
    &:hover{ 
        color:  $mainGreen;

    }

}
